/**
 * HTTP  axios 
 */
//vue 
import Vue from 'vue'
import Qs from 'qs'
import axios from 'axios';

axios.defaults.withCredentials=true;
axios.defaults.headers.post['Content-Type'] = 'application/json; charset=UTF-8';
//  process node API_URLdev.env require
var apiUrl=process.env.API_URL;
export default {
	//GET 
	get:function(api,params,callBack){
		axios.get(apiUrl+api,{
			params:params,
			// headers: {'Content-Type': 'application/json'},
            validateStatus: function (status) {
			    return (status ==200 || status ==401); // 500 reject
			}
		}).then((response) => {
			if(response.status==401){
				window.router.push("/");
			}else{
				callBack(response.data);
			}
		})
		.catch(function(response) {
			console.log(response);
			bootbox.alert({ 
			  size: "small",
			  message: "", 
			  callback: function(){
			  	//
			  }
			});
		});
	},
	//POST 
	post:function(api,params,callBack){
		axios({
            method: 'post',
            //headers: {'Content-Type': 'application/json; charset=UTF-8'},
            url: apiUrl+api,
            // data: Qs.stringify(params),
            data: params,
            validateStatus: function (status) {
			    return (status ==200 || status ==401); // 500 reject
			}
        }).then((response) => {
			if(response.status==401){
				window.router.push("/");
			}else{
				callBack(response.data);
			}
		 	
		})
		.catch(function(response) {
			console.log(response);
			bootbox.alert({ 
			  size: "small",
			  message: "", 
			  callback: function(){
			  	//
			  }
			});
		});
	},
	//
	postFile:function(api,params,callBack){
		//
		let _params=new FormData();
		for(let key in params){
			_params.append(key,params[key]);
		}
		axios({
            method: 'post',
            url: apiUrl+api,
            //headers: {'Authorization': token},
            data: _params,
            validateStatus: function (status) {
			    return (status ==200 || status ==401); // 500 reject
			},
			headers: {'Content-Type': 'multipart/form-data'}
        }).then((response) => {
			if(response.status==401){
				window.router.push("/");
			}else{
				callBack(response.data);
			}
		 	
		})
		.catch(function(response) {
			console.log(response);
			bootbox.alert({ 
			  size: "small",
			  message: "", 
			  callback: function(){
			  	//
			  }
			});
		});
	},
}
