/**
 * 
 */
import Vue from 'vue'
//Store
import store from 'src/stores/index.js'

//  v-focus
Vue.directive('func', {
  //  DOM 
  inserted: function (el,binding) {
    let funcList=store.state.user.func;
    let func=binding.value;
    if(funcList.filter((x)=>{ return func==x.func}).length==0){
    	//chorme
    	try{
    		el.remove();
    	}catch(e){

    	}
        //IE
    	try{
    		el.parentNode.removeNode(el);
    	}catch(e){

    	}
    	
    }
  }
})


