
var n = {
	after: function(n, e) {
		return " " + n + "" + e[0] + ""
	},
	alpha_dash: function(n) {
		return " " + n + ""
	},
	alpha_num: function(n) {
		return n + " ."
	},
	alpha_spaces: function(n) {
		return " " + n + " ."
	},
	alpha: function(n) {
		return " " + n + " ."
	},
	before: function(n, e) {
		return " " + n + " " + e[0] + " ."
	},
	between: function(n, e) {
		return " " + n + " " + e[0] + " " + e[1] + "."
	},
	confirmed: function(n, e) {
		return " " + n + " " + e[0] + "."
	},
	date_between: function(n, e) {
		return " " + n + "" + e[0] + "" + e[1] + "."
	},
	date_format: function(n, e) {
		return " " + n + "" + e[0] + "."
	},
	decimal: function(n, e) {
		void 0 === e && (e = ["*"]);
		var t = e[0];
		return " " + n + " " + ("*" === t ? "": t) + " ."
	},
	digits: function(n, e) {
		return " " + n + "  " + e[0] + ""
	},
	dimensions: function(n, e) {
		return " " + n + " " + e[0] + "  " + e[1] + " ."
	},
	email: function(n) {
		return " " + n + " ."
	},
	ext: function(n) {
		return " " + n + " ."
	},
	image: function(n) {
		return " " + n + " ."
	},
	in:function(n) {
		return " " + n + " ."
	},
	ip: function(n) {
		return " " + n + " ."
	},
	max: function(n, e) {
		return " " + n + " " + e[0] + "."
	},
	mimes: function(n) {
		return " " + n + " ."
	},
	min: function(n, e) {
		return " " + n + "  " + e[0] + " ."
	},
	not_in: function(n) {
		return " " + n + "."
	},
	numeric: function(n) {
		return " " + n + " ."
	},
	regex: function(n) {
		return " " + n + " ."
	},
	required: function(n) {
		return n + " ."
	},
	size: function(n, e) {
		return " " + n + "  " + e[0] + " KB."
	},
	url: function(n) {
		return " " + n + "url."
	}
};
export default {
	name:"zh_CN",
	messages: n,
	attributes: {}
};


