/**
 * 
 */
import { Validator } from 'vee-validate';
import HttpUtil from 'src/util/httpUtil.js';

/**
 * 
 */
Validator.extend('onlyLetterAndNumber', {
  messages: {
    zh_CN: (field, args) => {
      return  field+'a-zA-Z0-9';
    }
  },
  validate: (value,filed,data) => new Promise(resolve => {  
    let reg=/^[a-zA-Z0-9]*$/
  	//
  	resolve({
        valid: reg.test(value)
    });
  })
});



